const Letter = ({
  letter,
  onClick,
}: {
  letter: string;
  onClick: () => void;
}) => {
  const gridItems = Array.from({ length: 35 }, (_, i) => i + 1);
  return (
    <div
      className="grid grid-cols-5 grid-rows-7 gap-2 w-[500px] h-[700px] border-2 border-black"
      onClick={onClick}
    >
      {gridItems.map((item) => (
        <div
          key={item}
          className="flex items-center justify-center rounded-3xl bg-white font-bold"
        >
          {item}
        </div>
      ))}
    </div>
  );
};

export default Letter;
